.main-container {
    width: 250px;
    height: 400px;

    margin: auto;
    
    justify-content: center;
    align-items: center;
}

.window {
    height: 450px;
    width: 250px;
    overflow: hidden;
    position: absolute;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.all-items {
    height: 100%;
    display: flex;
    overflow: hidden;

    transition: translate;
    transition-property: transform;
    transition-duration: 300ms;
    transition-timing-function: ease-in-out;
    position: absolute;
}