.screen {
  width: 100%;
  height: 100%;
}
.right, .left {
  position: absolute;
  width: 5rem;
  height: 5rem;
  display: flex;
  margin-top: 10rem;

  background-color: white;
  border: none;
  border-radius: 50%;
  transition-duration: 0.4s;
  cursor: pointer;
}

.right:hover {
  background-color: gray
}

.left:hover {
  background-color: gray
}

.right {
  right: 0;
  margin-right: 10px;
}

.left {
  left: 0;
  margin-left: 10px;
}

.container {
  width: 250px;
  height: 400px;
  perspective: 1000px;
}

.front, .back {
  position: absolute;
  width: 250px;
  height: 400px;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 1s;
  backface-visibility: hidden;
  border-radius: 20px;
}

.front {
  background: linear-gradient(156deg, #0834f9 -50%, #f44b03 110%);
}

.back {
  background: linear-gradient(156deg, #CDDC39 2%, #9E9E9E 145%);
  transform: rotateY(180deg);
}
.container:hover .front {transform: rotateY(180deg);}
.container:hover .back {transform: rotateY(360deg);}

.Word-1 {
  color: lime;
  font-weight: 900;
}

.Word-2 {
  color: rebeccapurple;
  font-weight: 900;
}

